import { z } from 'zod';

// Zod schema for wallet analysis query parameters
export const WalletAnalysisParamsSchema = z.object({
  wallet: z.string().min(1, 'Wallet address is required'),
  blockchain: z.string().optional(),
  period: z
    .string()
    .optional()
    .default('7d')
    .transform((val) => val?.toLowerCase())
    .refine((val) => ['1d', '7d', '30d', '90d'].includes(val), {
      message: 'Period must be one of: 1d, 7d, 30d, 90d',
    }),
});

export type WalletAnalysisParams = z.input<typeof WalletAnalysisParamsSchema>;

// OpenAPI-compatible params schema (simplified without transformations)
export const WalletAnalysisParamsSchemaOpenAPI = z.object({
  wallet: z.string().min(1).describe('Wallet address to analyze'),
  blockchain: z.string().optional().describe('Blockchain ID (e.g., ethereum, base, solana:solana)'),
  period: z.string().optional().default('7d').describe('Analysis period: 1d, 7d, 30d, or 90d'),
});

// Zod schema for wallet analysis response
export const WalletAnalysisResponseSchema = z.object({
  data: z.object({
    winRateDistribution: z.object({
      '>500%': z.number(),
      '200%-500%': z.number(),
      '50%-200%': z.number(),
      '0%-50%': z.number(),
      '-50%-0%': z.number(),
      '<-50%': z.number(),
    }),
    marketCapDistribution: z.object({
      '>1000M': z.number(),
      '>100M': z.number(),
      '10M-100M': z.number(),
      '1M-10M': z.number(),
      '100k-1M': z.number(),
      '<100k': z.number(),
    }),
    periodTimeframes: z.array(
      z.object({
        date: z.date(),
        realized: z.number(),
      }),
    ),
    stat: z.object({
      totalValue: z.number(),
      periodTotalPnlUSD: z.number(),
      periodRealizedPnlUSD: z.number(),
      periodRealizedRate: z.number(),
      periodActiveTokensCount: z.number(),
      periodWinCount: z.number(),
      fundingInfo: z.object({
        from: z.string().nullable(),
        date: z.date().nullable(),
        chainId: z.string().nullable(),
        txHash: z.string().nullable(),
        amount: z.string().nullable(),
        fromWalletLogo: z.string().nullable(),
        fromWalletTag: z.string().nullable(),
      }),
      periodVolumeBuy: z.number(),
      periodVolumeSell: z.number(),
      periodBuys: z.number(),
      periodSells: z.number(),
      nativeBalance: z
        .object({
          rawBalance: z.string(),
          formattedBalance: z.number(),
          assetId: z.number().nullable(),
          chainId: z.string(),
          address: z.string(),
          decimals: z.number(),
          name: z.string(),
          symbol: z.string(),
          logo: z.string().nullable(),
          price: z.number(),
          balanceUSD: z.number(),
        })
        .nullable(),
      periodBuyTokens: z.number(),
      periodSellTokens: z.number(),
      periodTradingTokens: z.number(),
      holdingTokensCount: z.number(),
      holdingDuration: z.number(),
      tradingTimeFrames: z.number(),
      winRealizedPnl: z.number(),
      winRealizedPnlRate: z.number(),
      winToken: z
        .object({
          address: z.string(),
          chainId: z.string(),
          name: z.string(),
          symbol: z.string(),
          logo: z.string().nullable(),
          decimals: z.number(),
        })
        .nullable(),
    }),
    labels: z.array(z.string()),
  }),
});

export type WalletAnalysisResponse = z.infer<typeof WalletAnalysisResponseSchema>;

